@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}

.wrapper {
  width: 1280px;
  margin: 50px auto;
}

header .logo {
  font-weight: 600;
  font-size: 20px;
}

header ul.nav {
  float: right;
  list-style: none;
}

header ul.nav li {
  display: inline;
  margin-left: 20px;
  cursor: pointer;
  transition: opacity 500ms;
}

header ul.nav li:hover {
  opacity: 0.5;
}

header .shop-cart-button {
  float: right;
  cursor: pointer;
  transition: color 500ms ease;
  font-size: 25px;
  transition: color, transform 500ms ease;
  margin-left: 20px;
}

header .shop-cart-button:hover,
header .shop-cart-button.active {
  color: lightcoral;
  transform: scale(1.5);
}

.header-new {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.right-section {
  width: 300px;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
}

.left-section {
  display: flex;
  align-items: center;
}

header .shop-cart {
  position: absolute;
  top: 30px;
  right: 0px;
  width: 450px;
  background: #fafafa;
  -webkit-box-shadow: 0px 5px 10px 2px rgba(86, 86, 86, 0.2);
  -moz-box-shadow: 0px 5px 10px 2px rgba(86, 86, 86, 0.2);
  box-shadow: 0px 5px 10px 2px rgba(86, 86, 86, 0.2);
  z-index: 1000;

  padding: 20px;
  padding-bottom: 0;
  margin-top: 25px;
}

header .shop-cart .item {
  width: 100%;
  float: left;
  margin-bottom: 20px;
}

header .shop-cart .empty h2 {
  font-size: 15px;
  margin-bottom: 20px;
  text-align: center;
  color: transparent;
  background: #898989;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  text-shadow: 0px 3px 3px rgba(255,255,255,0.8);
}

header .shop-cart .item img {
  width: 70px;
  float: left;
  margin-right: 20px;
}

header .shop-cart .item h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

header .shop-cart .item b {
  color: #6a6a6a;
  font-weight: 600;
}

header .shop-cart .item .delete-icon {
  color: #6a6a6a;
  float: right;
  position: relative;
  top: 0;
  cursor: pointer;
  transition: color, transform 500ms ease;
}

header .shop-cart .item .delete-icon:hover {
  color: lightcoral;
  transform: scale(1.5);
}

header .shop-cart .summa {
  float: left;
  width: 100%;
  font-weight: 300;
  margin-bottom: 10px;
}

header .presentation {
  margin:  50px 0;
  background: url('./img/bg.jpg') no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;
}

header .presentation::after {
  content: 'Лучшие товары для твоего дома';
  position: absolute;
  top: 100px;
  left: 50px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  color: white;
}

header .presentation::before {
  content: 'премиального качества';
  position: absolute;
  top: 250px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: white;
}

.categories div {
  display: inline-block;
  width: 65px;
  text-align: center;
  background: #ededed;
  padding: 10px 20px;
  margin: 0 7px 25px 7px;
  cursor: pointer;
  -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
  -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
  box-shadow: 0px 5px 10px -5px rgba(34, 60, 80, 0.2) inset;
  transition: all 0.5s ease;
}

.categories div:hover {
  -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  transform: scale(1.05);
}

.full-item {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #2222229b;
  z-index: 9999;
  overflow: auto;
}

.full-item>div {
  width: 600px;
  position: relative;
  margin: 10% auto;
  padding: 15px 15px;
  background: white;
  border-radius: 2px;
}

main {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 40px;
}

main .item {
  width: 100%;
  background-color: #f5f5f5;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;
}

@media (max-width: 720px) {
  main {
    grid-template-columns: 1fr;
  }

  .wrapper {
    width: 360px;
  }
}

@media (min-width: 721px) and (max-width: 999px) {
  main {
    grid-template-columns: 1fr 1fr;
  }

  .wrapper {
    width: 720px;
  }
}

main .item img,
.full-item img {
  width: 100%;
  transition: transform 500ms ease;
  cursor: pointer;
}

main .item img:hover,
.full-item img:hover {
  transform: scale(1.05);
}

main h2, .full-item h2, 
main p, .full-item p,
main b, .full-item b {
  margin: 10px 20px;
  color: #333;
}

main b, .full-item b {
  color: #6a6a6a;
}

main .add-to-cart,
.full-item .add-to-cart {
  position: absolute;
  right:  15px;
  bottom: 15px;
  background-color: lightcoral;
  color: whitesmoke;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  font-weight: 600;
  cursor: pointer;
  transition: transform 500ms ease;
}
main .add-to-cart:hover,
.full-item .add-to-cart:hover {
  transform: scale(1.25) rotate(90deg);
}

footer {
  text-align: center;
  margin-top: 100px;
}
